/** @format */

@use "../utilities/colors" as *;
@use "../utilities/times" as *;

* {
  box-sizing: border-box !important;
  margin: 0;
  padding: 0;
  user-select: none;
}

html {
  scroll-behavior: smooth;

  a {
    color: $main-color;
    text-decoration: none;
  }

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
}

::-webkit-scrollbar {
  width: 0px;
}

.container {
  position: relative;
  @media (min-width: 1440px) {
    max-width: 1400px;
  }

  @media (max-width: 1439px) {
    max-width: 1200px;
    padding: 30px;
  }

  @media (max-width: 1199px) {
    padding: 15px;
  }
}

.line-effect {
  position: relative;
  text-decoration: none;
  display: inline-block;
  color: #000;

  &:after {
    content: "";
    position: absolute;
    width: 0;
    height: 1px;
    left: auto;
    right: 0;
    bottom: 3px;
    transition: width 0.6s cubic-bezier(0.25, 0.8, 0.25, 1) 0s;
    background-color: black !important;
  }

  &:hover {
    color: #000;

    &::after {
      width: 100%;
      left: 0;
      right: auto;
    }
  }
}

img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

.header-logo {
  font : {
    size: 40px;
    family: "Archivo Black";
  }
  text-transform: uppercase;
  color: black;
}

/** @format */

._button {
  width: 100%;
  cursor: pointer;

  a {
    display: inline-block;
    color: #000;
    padding: 4px 30px;
    position: relative;
    transition: all 0.3s ease;

    .back {
      position: relative;
      z-index: 1;
      display: block;
    }

    .front {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      color: #fff;
      white-space: nowrap;
      width: 60px;
      padding: 4px 30px;
      z-index: 1;
      overflow: hidden;
      transition: all 0.3s ease;
    }

    &:hover .front {
      width: 100%;
    }

    &:before {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      width: 60px;
      background-color: #999;
      transition: all 0.3s ease;
    }

    &:hover:before {
      width: 100%;
    }
  }
}

.text_hover_effect {
  position: relative;
  display: inline;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
  transition: background-size 0.5s;

  &::after {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
  }

  &:hover {
    background-size: 100% 1px;
  }
}

.section-inner {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 40px 30px 35px;
  position: relative;

  &:not(:last-child) {
    margin-bottom: 40px;
  }

  @media (max-width: 1439px) {
    padding: 40px 30px 20px;
  }

  .title {
    position: absolute;
    top: -20px;

    h3 {
      font-size: 15px;
      text-transform: uppercase;
      display: inline-block;
      background-color: #efefef;
      padding: 10px 30px 9px 30px;
      color: black;
      font-weight: 700;

      @media (max-width: 768px) {
        font-size: 14px;
      }
    }
  }

  .title-section {
    font-size: 14px;
    text-transform: uppercase;
    color: black;
    font-weight: 650;
    padding-bottom: 10px;

    &:not(.heading) {
      font-size: 13px;
    }
  }
}

.popup-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 2000;
  cursor: zoom-out;
}

.popUp {
  position: fixed;
  z-index: 2001;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;

  @media (max-width: 767px) {
    width: 370px;
  }

  img {
    width: 930px;
    max-width: 100%;
    transition: $transition3;

    border-radius: 12px;

    cursor: grab;

    &:hover {
      transform: scale(1.1);
    }
  }
}

.contacts {
  .section-inner {
    margin-bottom: 40px;
  }
}

